/* Set the app default global progress bar component position */
.progress-linear {
  height: 4px !important;
  position: fixed;
  top: 0;
  z-index: 5;
  margin-top: 0;
}
.main-container {
  margin-top: 0px;
  padding-top: 0px !important;
}
.progress-linear {
  z-index: 5;
  padding-top: 0px;
}
/* Hide the app default global progress when it is disabled */
.progress-linear-disabled {
  display: none;
}
/* Set the app tab padding */
.app-tab {
  padding: 10px;
}
.application--wrap {
  overflow: hidden;
}
.container.page-root {
  height: 100%;
  padding-top: 100px !important;
}
@media (min-width: 1000px) {
.app-tab {
    padding: 25px;
}
}
/* Set the page root container padding according the media size  */
@media (max-width: 599px) {
.container.page-root {
    padding: 5px;
}
}
@media (min-width: 600px) and (max-width: 999px) {
.container.page-root {
    padding: 10px;
}
}
@media (min-width: 1000px) {
.container.page-root {
    padding: 20px;
}
}
