/* ## THEME CUSTOMIZATION ## */
/* Customize the default font */
* {
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
/* Set the app default font for box components.
We define it here because we want this as a default global definition */
.box-title {
  font-family: "Raleway", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.headline {
  display: block;
  width: 100%;
  text-align: left;
}
.use-and-data-policy {
  margin-top: 70px;
  width: 100%;
  margin-bottom: -70px;
  z-index: 3;
}
.v-alert .use-and-data-policy-btn {
  color: white;
  font-weight: 600;
}
.data-and-privacy-link {
  color: black !important;
}
.use-and-data-policy-btn .v-btn__content {
  color: black;
}
